<script>
import InfiniteScroller from '../infinite'
import Vue from 'vue'

function ContentSource (fetch, itemRender, Tombstone) {
  this.itemRender = itemRender
  this.Tombstone = Tombstone
  this.fetch = fetch
  this.nextItem_ = 0
  this.baseNode = document.createElement('div')
}

ContentSource.prototype = {
  createTombstone () {
    const vm = new Vue({
      el: this.baseNode.cloneNode(true),
      render: (h) => {
        return h(this.Tombstone)
      }
    })
    return vm.$el
  },
  render (data, el, item) {
    const vm = new Vue({
      el: el || this.baseNode.cloneNode(true),
      render: (h) => {
        return h(this.itemRender, {
          props: {
            data: data
          }
        })
      }
    })
    item.vm = vm
    return vm.$el
  }
}

export default {
  props: {
    fetch: Function,
    item: Object,
    tombstone: Object
  },
  render (h) {
    return h('div', {
      a: 1
    }, this.$slots.default)
  },
  data () {
    return {
      contentSource: new ContentSource(this.fetch, this.item, this.tombstone),
      scroller: null
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      this.scroller = new InfiniteScroller(
        this.$el,
        this.contentSource,
        {
          RUNWAY_ITEMS: 10,
          RUNWAY_ITEMS_OPPOSITE: 10
        }
      )
    }
  }
}
</script>



