<style>
html {
  background: #eeeeee;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  overflow: hidden;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
#chat-timeline {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  width: 100%;
  height: 100%;
  position: absolute;
  box-sizing: border-box;
  contain: layout;
  will-change: transform;
}
.switch {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 100;
  background: #03A9F4;
  color: #fff;
  font-size: 14px;
  padding: 10px;
}
</style>

<template>
  <div id="app">
    <div class="switch" @click="toggle">{{ view ? 'Recylce' : 'Common' }}</div>
    <CycleList v-if="view" id="chat-timeline" :fetch="fetch" :item="ChatItem" :tombstone="Tombstone" ></CycleList>
    <CommonList v-if="!view" id="chat-timeline" ></CommonList>
  </div>
</template>

<script>
import CycleList from './components/CycleList'
import ChatItem from './components/ChatItem.vue'
import Tombstone from './components/Tombstone'
import initStat from './stat-config'
import fetch from './fetch'
import CommonList from './components/CommonList.vue'

export default {
  name: 'app',
  data () {
    return {
      view: true,
      messages: [],
      fetch,
      ChatItem,
      Tombstone
    }
  },
  mounted () {
    initStat()
  },
  components: {
    CycleList,
    CommonList
  },
  methods: {
    toggle () {
      this.view = !this.view
    }
  }
}
</script>



